import React from 'react';
import Products from './Products';
import Layout from '../../components/Layout';

async function action({ fetch }) {

  return {
    title: 'React Starter Kit',
    component: (
      <Layout>
        <Products />
      </Layout>
    ),
  };
}

export default action;



